import { Component, OnInit, Input } from '@angular/core';
import { Headers, RequestOptions, Http } from '@angular/http';
import { SgMetadata } from './sgmetadata/sgMetadata';
import { MetadataDto, GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { isNullOrUndefined } from 'util';
import { host } from './commonobject/global';
import { ObjectDict } from './commonobject/apisummary';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { CacheService } from '../../../../../reference/farris/farris.idedevkit.ref';
import { ApiService } from './apiservice/apiservice';

@Component({
  selector: 'app-api',
  templateUrl: './sg.component.html',
  styleUrls: ['./sg.component.css']
})
export class SgDesignerComponent implements OnInit {
  public apimetadata: SgMetadata;
  public metadataDto: MetadataDto;
  private _metadataContnet: MetadataDto;
  private _metadataUri: string;
  public resourceTypeList: string[] = []; //

  set metadataUri(metadataUri: string) {
    this._metadataUri = metadataUri;
    // todo: 
    const fileName = metadataUri.substring(metadataUri.lastIndexOf('/') + 1);
    const filePath = metadataUri.substring(1, metadataUri.lastIndexOf('/'));
    this.metadataService.LoadMetadata(fileName, filePath).subscribe(value => {
      this.metadataContent = value;
    });
  }

  set metadataContent(metadataContent: any) {
    this.loadSgMetadata(metadataContent);
  }

  constructor(private http: Http, private metadataService: GSPMetadataService,private cache:CacheService,private apiService:ApiService) { }

  ngOnInit() {
    this.apimetadata=new SgMetadata();
    this.getResourceTypeList();
  }

  //
  private getResourceTypeList() {
    if (this.resourceTypeList.length > 0) {
      return;
    }
    if (this.resourceTypeList.length == 0) {
      let url = host + "/api/dev/main/v1.0/sg/resource-types";
      this.http.get(url).toPromise().then(response => {
        if (response.json()) {
          this.resourceTypeList = response.json();
        }
      })
    }
  }

  //SG
  private loadSgMetadata(dto: MetadataDto) {
    this.metadataDto = dto;
    if (dto.content && dto.content != "") {
      let jsonObj = JSON.parse(dto.content);
      this.apiService.GetCamelParse(jsonObj);
      this.apimetadata = jsonObj;
      if (isNullOrUndefined(this.apimetadata.code)) {
        this.apimetadata.code = dto.code;
      }
      if (isNullOrUndefined(this.apimetadata.name)) {
        this.apimetadata.name = dto.name;
      }
      if (isNullOrUndefined(this.apimetadata.version)) {
        this.apimetadata.version = '1.0';
      }
      if (isNullOrUndefined(this.apimetadata.bizObject)) {
        this.apimetadata.bizObject = dto.bizobjectID;
      }
    }
  }

  //Api
  private saveApiMetadata() {
    let result = this.httpMethodNullCheck(this.apimetadata);
    if (result.key == "false") {
      alert(result.value);
      return;
    }
    let pathCheck = this.pathCheck(this.apimetadata);
    if (pathCheck.key == "false") {
      alert(pathCheck.value);
      return;
    }
    let stringcontent = JSON.stringify(this.apimetadata);
    this.metadataDto.content = stringcontent;
    let sessionId=this.cache.get('sessionId');
    this.metadataService.SaveMetadata(this.metadataDto,sessionId).subscribe(response=>{
        if(response.status==200){
           alert("");
        }
        else{
          alert("!");
        }
    })
    // let content = {
    //   "ID": this.metadataDto.id,
    //   "NameSpace": this.metadataDto.nameSpace,
    //   "Name": this.metadataDto.name,
    //   "Type": this.metadataDto.type,
    //   "BizobjectID": this.metadataDto.bizobjectID,
    //   "RelativePath": this.metadataDto.relativePath,
    //   "ExtendProperty": this.metadataDto.extendProperty,
    //   "Content": this.metadataDto.content
    // }
    // let headers = new Headers();
    // headers.append('Content-Type', 'application/json');
    // headers.append('Authorization', 'as');
    // headers.append('Access-Control-Allow-Origin', '*');
    // const options = new RequestOptions({
    //   headers: headers
    // });
    // let url = host + "/api/dev/main/v1.0/metadatas";
    // this.http.put(url, content, options).toPromise().then(response => {
    //   if (response.status == 200) {
    //     alert("");
    //   }
    //   else {
    //     alert("");
    //   }
    // })
  }

  //REST
  private getApiDocument(){
    alert('');
    // let relativePath=this.metadataDto.relativePath;
    // let metadataId=this.metadataDto.id;
    // let url= host + "/api/dev/main/v1.0/sg/apidocuments?relativePath="+relativePath+"&metadataId="+metadataId+"&serverIp="+host;
    // this.http.get(url).toPromise().then(response=>{
    //    if(response.status==200){
    //      alert("aaa");
    //    }
    //    else{
    //     alert("bbb");
    //    }
    // })
  }

  private httpMethodNullCheck(metadata: SgMetadata): ObjectDict<string> {
    let validResultDict = {} as ObjectDict<string>;
    let tempStatus = false;
    if (this.apimetadata.service && this.apimetadata.service.operations && this.apimetadata.service.operations.length > 0) {
      this.apimetadata.service.operations.forEach(operation => {
        if (tempStatus ==false) {
          if (isNullOrUndefined(operation.httpMethod) || isNullOrEmptyString(operation.httpMethod)) {
            validResultDict.key = "false";
            validResultDict.value = "" + operation.code + "HttpMethod";
            tempStatus = true;
          }
        }
      });
    }
    if (tempStatus == false) {
      validResultDict.key = "true";
      validResultDict.value = "";
    }
    return validResultDict;
  }

  private pathCheck(metadata: SgMetadata): ObjectDict<string> {
    let validResultDict = {} as ObjectDict<string>;
    let tempStatus = false;
    if (this.apimetadata.service && this.apimetadata.service.operations && this.apimetadata.service.operations.length > 0) {
      this.apimetadata.service.operations.forEach(operation => {
        if (tempStatus == false) {
          if (operation && operation.parameters && operation.parameters.length > 0) {
            operation.parameters.forEach(param => {
              if (param.in == "Path") {
                let index = operation.template.indexOf("{" + param.code + "}");
                if (index == -1) {
                  validResultDict.key = "false";
                  validResultDict.value = "" + operation.code + "{" + param.code + "}";
                  tempStatus = true;
                }
              }
            });
          }
        }
      });
    }
    if (tempStatus == false) {
      validResultDict.key = "true";
      validResultDict.value = "";
    }
    return validResultDict;
  }
}



