import { Component, Input, OnInit, EventEmitter, ComponentFactoryResolver, Injector, ElementRef, ViewChild } from '@angular/core';
import { SgMetadata, ServiceDefine, ServiceParameter, ServiceOperation, SgModel, SgModelType, } from '../sgmetadata/sgMetadata';
import { ApiService } from '../apiservice/apiservice';
import { RowArgs, SelectAllCheckboxState } from '@progress/kendo-angular-grid';
import { DialogService, DialogCloseResult, DialogAction } from '@progress/kendo-angular-dialog';
import { ResourceTypeSelectionComponent } from './modal/resourceselection.component';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { Http } from '@angular/http';
import { host } from '../commonobject/global';
import { ResourceOpSelectionComponent } from './modal/resourceopselection.component';
import { SummaryInfo } from '../commonobject/summaryInfo';
import { ParamEditDialogComponent } from './modal/paramedit.component';
import { GSPMetadataService, SelectMetadataComponent, MetadataDto } from '../../../../../../reference/metadata/metadata.ref';
import { BsModalService } from '../../../../../../reference/farris/farris.ui.ref';
import { ClipboardService } from 'ngx-clipboard';

declare var window: any;
declare var $: any;

interface Item {
  text: string,
  value: number
}

@Component({
  selector: 'app-servicedefine',
  templateUrl: './servicedefine.component.html'
})
export class ServiceDefineComponent implements OnInit {
  @Input() apimetadata: SgMetadata;
  @Input() resourceTypeList: string[];
  @Input() metadataDto:MetadataDto;
  selectedServiceOpParamList: ServiceParameter[] = []; //
  currentServiceParamList: ServiceParameter[] = []; //
  selectedServiceOpList: ServiceOperation[] = []; //
  currentServiceOperation: ServiceOperation = {} as ServiceOperation; //
  currentServiceOpParam: ServiceParameter; //
  isRowSelected = (e: RowArgs) => this.selectedServiceOpIds.indexOf(e.dataItem.id) >= 0;
  isRowParamSelected = (e: RowArgs) => this.selectedServiceParamIds.indexOf(e.dataItem.id) >= 0;
  selectedServiceOpIds: string[] = [];
  selectAllServiceOpState: SelectAllCheckboxState = 'unchecked';
  selectedServiceParamIds: string[] = [];
  paramsPosition: Array<string> = ['Body', 'Header', 'Path', 'Query'];
  paramsPositionWithoutBody: Array<string> = ['Header', 'Path', 'Query'];
  httpmethods: Array<string> = ['GET', 'POST', 'DELETE', 'PATCH', 'PUT'];
  status: Array<boolean> = [true, false];
  transfItems: Array<Item> = [
    { text: "RequiresNew", value: 0 },
    { text: "Required", value: 1 },
    { text: "NotSupport", value: 2 }];
   selectedMetadataType: string;
   showMetadataSelectDialog: boolean = false;
   selectedResourceType: string;
   isExistResource:boolean=false;
   currentServiceOperationUrl:string;

  ngOnInit() {
    if (!this.apimetadata.service) {
      this.apimetadata.service = {} as ServiceDefine;
      this.apimetadata.service.operations = [] as ServiceOperation[];
    }
    if (!this.apimetadata.service.id) {
      this.apimetadata.service.id = this.apiService.getGuid();
    }
    if (!this.apimetadata.service.operations) {
      this.apimetadata.service.operations = [] as ServiceOperation[];
    }
    if(this.apimetadata.service.resourceId){
      this.isExistResource=true;
    }
    this.currentServiceOperation = this.apimetadata.service.operations.length > 0 ? this.apimetadata.service.operations[0] : {} as ServiceOperation;
    if (this.apimetadata.service.operations.length > 0) {
      this.currentServiceParamList = this.apimetadata.service.operations[0].parameters;
      this.selectedServiceOpIds = [];
      this.selectedServiceOpIds.push(this.apimetadata.service.operations[0].id);
      if (this.apimetadata.service.operations.length == 1) {
        this.selectAllServiceOpState = 'checked';
      }
      if (this.apimetadata.service.operations.length > 1) {
        this.selectAllServiceOpState = 'indeterminate';
      }
      this.selectedServiceOpList = [] as ServiceOperation[];
      this.selectedServiceOpList.push(this.apimetadata.service.operations[0]);
    }
    this.getUri(this.currentServiceOperation);
  }

  constructor(private apiService: ApiService, private dialogService: DialogService, private http: Http,private metadataService:GSPMetadataService,
    private modalService:BsModalService,private resolver:ComponentFactoryResolver,private injector:Injector,private elementRef: ElementRef,private _clipboardService: ClipboardService) {
    this.dialogService.open
  }

  //
  selectResourceType() {
    if (!isNullOrEmptyString(this.apimetadata.service.resourceId) && !isNullOrUndefined(this.apimetadata.service.resourceId)) {
      alert("");
      return;
    }
    const dialogRef = this.dialogService.open({
      title: '',
      content: ResourceTypeSelectionComponent,
      actions: [
        { text: '' },
        { text: '', primary: true }
      ],
      width: 400
    });
    const resIntance = dialogRef.content.instance;
    resIntance.resourceTypeList = this.resourceTypeList;
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      let selectedResourceType = dialogRef.content.instance.selectedResourceType;
      if (action.text == "") {
        if (!isNullOrUndefined(selectedResourceType) || !isNullOrEmptyString(selectedResourceType)) {
          actionEmitter.emit(action);
        }
        else {
          alert("");
        }
      }
      else {
        actionEmitter.emit(action);
      }
    });
    dialogRef.result.subscribe((dialogResult) => {
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "") {
          this.selectedResourceType = dialogRef.content.instance.selectedResourceType;
          this.openMetadataSelect();
        }
      }
    });
  }

  openMetadataSelect(){
    const compFactory=this.resolver.resolveComponentFactory(SelectMetadataComponent);
    const compRef=compFactory.create(this.injector);
    compRef.instance.metadataPath=this.metadataDto.relativePath;
    if (this.selectedResourceType == "VO") {
      compRef.instance.typeName="GSPViewModel";
    }
    else{
      alert("");
      return;
    }
    compRef.instance.selectedMetadata.subscribe($event => {
      let selectedMetadata=$event;
      this.getSelectedMetadataMethods(selectedMetadata);
    });
    const modalConfig = {
      title: '',
      width: 882,
      height: 573,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe($event => {
      dialog.close();
    });
  }

  // 
  getSelectedMetadataMethods(selectedMetadata: any) {
    if (!selectedMetadata || !selectedMetadata.metadata.id) {
      alert("");
      return;
    }
    let relativePath = selectedMetadata.metadata.relativePath;
    let fileName = selectedMetadata.metadata.fileName;
    let combinationResourceInfo = encodeURIComponent(fileName + "#" + relativePath);
    this.addOperations(this.selectedResourceType, combinationResourceInfo);
  }

  //
  private addOperations(resourceType: string, combinationResourceInfo: string) {
    debugger;
    let url = host + "/api/dev/main/v1.0/sg/resources/operations/?resourceType=" + resourceType + "&resourceId=" + combinationResourceInfo;
    this.http.get(url).toPromise().then(response => {
      if (response.status == 200) {
        let summaryList = response.json();
        const dialogRef = this.dialogService.open({
          title: '',
          content: ResourceOpSelectionComponent,
          actions: [
            { text: '' },
            { text: '', primary: true }
          ],
          width: 550
        });
        const resOpSelectionInstance = dialogRef.content.instance;
        resOpSelectionInstance.opSummaryList = summaryList;
        resOpSelectionInstance.apimetadata = this.apimetadata;
        const actionEmitter = dialogRef.dialog.instance.action;
        dialogRef.dialog.instance.action = new EventEmitter<any>();
        const sub = dialogRef.dialog.instance.action.subscribe(action => {
          if (action.text == "") {
            let selectedOpSummaryList = dialogRef.content.instance.selectedOpSummaryList;
            if (selectedOpSummaryList.length <= 0) {
              alert("");
              return;
            }
            let isExistSameOperation: boolean = false;
            if (this.apimetadata.service && this.apimetadata.service.operations.length > 0) {
              for (let i = 0; i < selectedOpSummaryList.length; i++) {
                if (isExistSameOperation == false) {
                  let tempIndex = this.apimetadata.service.operations.findIndex(vaue => vaue.resourceOpId == selectedOpSummaryList[i].id);
                  if (tempIndex != (-1)) {
                    alert("" + this.apimetadata.service.operations[tempIndex].code + "");
                    isExistSameOperation = true;
                    return;
                  }
                }
              }
            }
            if (isExistSameOperation == false) {
              actionEmitter.emit(action);
            }
          }
          else if (action.text == "") {
            actionEmitter.emit(action);
          }
        });
        dialogRef.result.subscribe((dialogResult) => {
          sub.unsubscribe();
          if (!(dialogResult instanceof DialogCloseResult)) {
            let actionName = (dialogResult as DialogAction).text;
            if (actionName == "") {
              this.getResourceInfo(resourceType, combinationResourceInfo);
              this.opSelectionConfirmHandler(dialogRef.content.instance.selectedOpSummaryList,resourceType, combinationResourceInfo)
            }
          }
        });
      }
    })
  }

  //
  private getResourceInfo(resourceType: string, combinationResourceInfo: string) {
    let url = host + "/api/dev/main/v1.0/sg/resources/?resourceType=" + this.selectedResourceType + "&resourceId=" + combinationResourceInfo;
    this.http.get(url).toPromise().then(response => {
      if (response.status == 200) {
        let summaryInfo: SummaryInfo = response.json();
        if (!this.apimetadata.service) {
          this.apimetadata.service = {} as ServiceDefine;
        }
        this.apimetadata.service.resourceType = resourceType;
        this.apimetadata.service.resourceId = summaryInfo.id;
        this.apimetadata.service.resourceCode = summaryInfo.code;
        this.apimetadata.service.resourceName = summaryInfo.name;
        this.apimetadata.service.resourcePath = decodeURIComponent(combinationResourceInfo).substring(decodeURIComponent(combinationResourceInfo).lastIndexOf('#')+1);
      }
    })
  }

  //
  private opSelectionConfirmHandler(selectedSummaryList: SummaryInfo[], resourceType:string, combinationResourceInfo: string) {
    let url = host + "/api/dev/main/v1.0/sg/resources/operations?resourceType=" + resourceType + "&resourceId=" + combinationResourceInfo;
    let i = 0;
    let opIdListStr = "";
    selectedSummaryList.forEach(element => {
      if (i == 0) {
        opIdListStr = encodeURIComponent(element.id);
        i++;
      }
      else {
        opIdListStr = opIdListStr + "," + encodeURIComponent(element.id);
        i++;
      }
    });
    if (opIdListStr != "") {
      url = url + "&opIdListstr=" + opIdListStr;
    }
    this.http.get(url).toPromise().then(response => {
      if (response.status == 200) {
        let operations = response.json() as ServiceOperation[];
        this.updateResDefineList(operations);
        this.getServiceModelParam(operations);
        this.isExistResource=true;
      }
    })
  }

  //
  private updateResDefineList(operations: ServiceOperation[]) {
    if (!this.apimetadata.service || !this.apimetadata.service.operations) {
      this.apimetadata.service.operations = [] as ServiceOperation[];
    }
    operations.forEach(element => {
      this.apimetadata.service.operations.push(element);
    });
    let length = this.apimetadata.service.operations.length;
    this.currentServiceOperation = this.apimetadata.service.operations[length - 1];
    this.getUri(this.currentServiceOperation);
    this.currentServiceParamList = this.currentServiceOperation.parameters;
    this.resetServiceSelection(this.currentServiceOperation.id);
    this.selectedServiceOpList = [] as ServiceOperation[];
    this.selectedServiceOpList.push(this.apimetadata.service.operations[length - 1]);
    if (this.selectedServiceOpIds.length < this.apimetadata.service.operations.length) {
      this.selectAllServiceOpState = 'indeterminate';
    }
    else if (this.selectedServiceOpIds.length == this.apimetadata.service.operations.length) {
      this.selectAllServiceOpState = 'checked';
    }
  }

  //
  private getServiceModelParam(operations: ServiceOperation[]) {
    operations.forEach(element => {
      element.parameters.forEach(element1 => {
        if (!element1.isBasicType) {
          if (!this.apimetadata.models) {
            this.apimetadata.models = [] as SgModel[];
          }
          if (this.apimetadata.models.length <= 0) {
            let temmModel: SgModel = {} as SgModel;
            temmModel.id = this.apiService.getGuid();
            temmModel.code = element1.modelName;
            temmModel.name = element1.modelName;
            temmModel.modelId = element1.modelId;
            if (this.selectedResourceType == "VO") {
              if (temmModel.modelId.indexOf("#") != (-1)) {
                temmModel.modelType = SgModelType.Dto;
              }
              else {
                temmModel.modelType = SgModelType.VO;
              }
            }
            this.apimetadata.models.push(temmModel);
          }
          else {
            let tempIndex = this.apimetadata.models.findIndex(value => value.modelId == element1.modelId);
            if (tempIndex == (-1)) {
              let temmModel: SgModel = {} as SgModel;
              temmModel.id = this.apiService.getGuid();
              temmModel.code = element1.modelName;
              temmModel.name = element1.modelName;
              temmModel.modelId = element1.modelId;
              if (this.selectedResourceType == "VO") {
                if (temmModel.modelId.indexOf("#") != (-1)) {
                  temmModel.modelType = SgModelType.Dto;
                }
                else {
                  temmModel.modelType = SgModelType.VO;
                }
              }
              this.apimetadata.models.push(temmModel);
            }
          }
        }
      });
    });
  }

   //
   private editParamList(param: ServiceParameter) {
    this.resetServiceParamSelection(param);
    const dialogRef = this.dialogService.open({
      title: '',
      content: ParamEditDialogComponent,
      actions: [
        { text: '' },
        { text: '', primary: true }
      ],
      width: 400
    });
    const instance = dialogRef.content.instance;
    instance.serviceParameter= Object.assign({},param);
    if(this.currentServiceOperation.httpMethod=='GET'||this.currentServiceOperation.httpMethod=="DELETE"){
      instance.paramsPosition=this.paramsPositionWithoutBody;
    }
    else{
      instance.paramsPosition=this.paramsPosition;
    }
    const actionEmitter = dialogRef.dialog.instance.action;
    dialogRef.dialog.instance.action = new EventEmitter<any>();
    const sub = dialogRef.dialog.instance.action.subscribe(action => {
      if (action.text == "") {
        let modifiedParam=dialogRef.content.instance.serviceParameter;
        let tempIndex=this.currentServiceParamList.findIndex(value=>value.id!=modifiedParam.id&&value.code==modifiedParam.code);
        if(tempIndex!=(-1)){
          alert(""+modifiedParam.code+"");
          return;
        }
        actionEmitter.emit(action);
      }
      else  if (action.text == "") {
        actionEmitter.emit(action);
      }
    });
    dialogRef.result.subscribe((dialogResult) => {
      sub.unsubscribe();
      if (!(dialogResult instanceof DialogCloseResult)) {
        let actionName = (dialogResult as DialogAction).text;
        if (actionName == "") {
          let modifiedParam=dialogRef.content.instance.serviceParameter;
          this.apiService.replaceSpecifiedElementById(this.currentServiceOperation.parameters,modifiedParam);
        }
      }
    });
  }

  //
  private addServiceOperation() {
    if (isNullOrEmptyString(this.apimetadata.service.resourceId) || isNullOrUndefined(this.apimetadata.service.resourceId)) {
      alert("");
      return;
    }
    let relativePath = this.apimetadata.service.resourcePath;
    let fileName ='';
    if(this.apimetadata.service.resourceType=='VO'){
      fileName=this.apimetadata.service.resourceCode+'.vo';
    }
    else{
      alert('');
      return;
    }
    let combinationResourceInfo = encodeURIComponent(fileName + "#" + relativePath);
    this.addOperations(this.apimetadata.service.resourceType, combinationResourceInfo);
  }

  //
  private resetServiceParamSelection(dataTtem: any) {
    this.selectedServiceParamIds = [];
    this.selectedServiceParamIds.push(dataTtem.id);
  }

  //
  private resetServiceSelection(input: any) {
    this.selectedServiceOpIds = [];
    this.selectedServiceOpIds.push(input);
  }

  //
  clearServiceDefine() {
    this.apimetadata.service = {} as ServiceDefine;
    this.apimetadata.service.operations = [];
    this.apimetadata.models = [];
    this.currentServiceOperation = {} as ServiceOperation;
    this.currentServiceParamList = {} as ServiceParameter[];
    this.selectAllServiceOpState="unchecked";
    this.currentServiceOperation.httpMethod=null;
    this.currentServiceOperation.transactionOption=null;
    this.isExistResource=false;
    this.currentServiceOperationUrl='';
  }

  //
  private deleteServiceOpList() {
    if (!this.selectedServiceOpList || this.selectedServiceOpList.length <= 0) {
      alert("");
      return;
    }
    if (this.selectedServiceOpList && this.selectedServiceOpList.length > 0) {
      this.selectedServiceOpList.forEach(element => {
        this.apiService.deleteSpecifiedElement(this.apimetadata.service.operations, element);
        this.apiService.deleteSpecifiedElement(this.selectedServiceOpIds, element.id);
      });
    }
    this.selectedServiceOpList = [] as ServiceOperation[];
    this.currentServiceParamList = [] as ServiceParameter[];
    if (this.apimetadata.service.operations && this.apimetadata.service.operations.length > 0) {
      this.apimetadata.service.operations[0].parameters.forEach(element => {
        this.currentServiceParamList.push(element);
      });
      this.currentServiceOperation = this.apimetadata.service.operations[0];
      this.getUri(this.currentServiceOperation);
    }
    else {
      this.currentServiceParamList = [] as ServiceParameter[];
      this.currentServiceOperation = {} as ServiceOperation;
      this.currentServiceOperationUrl='';
    }
    this.selectAllServiceOpState = 'unchecked';
  }

  //
  private modifyParamHandler(param: ServiceParameter) {
    //id
    if (!param.id) {
      if (!this.currentServiceOperation.parameters || this.currentServiceOperation.parameters.length <= 0) {
        this.currentServiceOperation.parameters = [] as ServiceParameter[];
      }
      param.id = this.apiService.getGuid();
      this.currentServiceOperation.parameters.push(param);
      this.currentServiceParamList = this.currentServiceOperation.parameters;
    }
    else {
      this.currentServiceOpParam = Object.assign({}, param);
      this.apiService.replaceSpecifiedElementById(this.currentServiceOperation.parameters, this.currentServiceOpParam);
    }
  }

  //
  getCurrentOperation({ dataItem }: any) {
    //this.resetServiceSelection(dataItem);
    let serviceOp = dataItem;
    this.currentServiceOperation = {} as ServiceOperation;
    this.currentServiceOperation = serviceOp;
    this.currentServiceParamList = [] as ServiceParameter[];
    this.currentServiceParamList = serviceOp.parameters;
    this.selectedServiceParamIds = [];
    this.getUri(this.currentServiceOperation);
  }

  //
  onSelectedServiceOpChange(e) {
    this.selectedServiceOpList = [] as ServiceOperation[];
    let len = this.selectedServiceOpIds.length;
    if (len > 0) {
      this.selectedServiceOpIds.forEach(id => {
        if (this.apimetadata.service && this.apimetadata.service.operations && this.apimetadata.service.operations.length > 0) {
          let tempIndex = this.apimetadata.service.operations.findIndex(value => value.id == id);
          if (tempIndex != (-1)) {
            this.selectedServiceOpList.push(this.apimetadata.service.operations[tempIndex]);
          }
        }
      });
    }
    if (len === 0) {
      this.selectAllServiceOpState = 'unchecked';
    } else if (len > 0 && len < this.apimetadata.service.operations.length) {
      this.selectAllServiceOpState = 'indeterminate';
    } else {
      this.selectAllServiceOpState = 'checked';
    }
  }

  //
  private onSelectAllServiceOpChange(checkedState: SelectAllCheckboxState) {
    if (checkedState === 'checked') {
      this.selectedServiceOpIds = this.apimetadata.service.operations.map((item) => item.id);
      this.selectAllServiceOpState = 'checked';
    } else {
      this.selectedServiceOpIds = [];
      this.selectAllServiceOpState = 'unchecked';
    }
  }

  //cellClick
  cellClickHandler({ dataItem }) {
    this.resetServiceParamSelection(dataItem);
  }

  getUri(operation:ServiceOperation){
    if(operation&&!isNullOrUndefined(operation.id)){
      this.getOperaionUri(operation);
    }
  }

  modifyOpTemplate(){
    this.getOperaionUri(this.currentServiceOperation);
  }

  getOperaionUri(operation:ServiceOperation):string{
        let appName=this.apimetadata.criticalApplication;
        let unitName=this.apimetadata.serviceUnit;
        let routeUrlName=this.apimetadata.routeUrl;
        let version=this.apimetadata.version;
        if(isNullOrUndefined(appName)||isNullOrUndefined(unitName)||isNullOrUndefined(routeUrlName)||isNullOrUndefined(version)){
          return '';
        }
        let baseRoute=`api/${appName}/${unitName}/v${version}/${routeUrlName}`;
        this.currentServiceOperationUrl=baseRoute;
        if(operation){
          if(operation.template!=''&&!isNullOrEmptyString(operation.template)&&!isNullOrUndefined(operation.template))
          if(operation.template.startsWith('/')){
            this.currentServiceOperationUrl=this.currentServiceOperationUrl+`${operation.template}`;
          }
          else{
            this.currentServiceOperationUrl=this.currentServiceOperationUrl+`/${operation.template}`;
          }
        }
        return this.currentServiceOperationUrl;
  }

  //Uri
  copyUri(){
    this._clipboardService.copyFromContent(this.currentServiceOperationUrl);
    alert("");
  }

  //
  openResource() {
    alert("UI")
  }
}


