export class SgMetadata {
    id:string;
    code:string;
    name:string;
    version:string;
    bizObject:string;
    description:string;
    criticalApplication:string;
    serviceUnit:string;
    routeUrl:string;
    service:ServiceDefine;
    models:SgModel[];
}

export class ServiceDefine{
    id:string;
    resourceId:string;
    resourceType:string;
    resourceCode:string;
    resourceName:string;
    resourcePath:string;
    operations:ServiceOperation[];
}

export class ServiceOperation{
    id:string;
    code:string;
    name:string;
    description:string;
    httpMethod:string;
    template:string;
    isDeprecated:boolean;
    resourceOpId:string;
    parameters:ServiceParameter[];
    transactionOption:TransactionOption;
    tranactionProvider:string;
    extend1:string;
    extend2:string;
    AssemblyInfo:AssemblyInfo;
}

export class AssemblyInfo{
    AssemblyName:string;
    FullName:string;
}

export class ServiceParameter{
    id:string;
    code:string;
    name:string;
    description:string;
    isRetVal:boolean;
    isCollection:boolean;
    isBasicType:boolean;
    modelId:string;
    modelName:string;
    listDeepth:number;
    in:string;
    isRequired:string;
}

export enum TransactionOption{
    requiresNew,
    required,
    notSupport
}

export class SgModel {
    id:string;
    code:string;
    name:string;
    modelId:string;
    modelType:SgModelType;
    modelTypeName:string;
}

export enum SgModelType{
    VO,
    Dto
}

export class ResourceSummary{
    id:string;
    code:string;
    name:string;
}


