  title = 'apibaseinfo';
  private selectedServiceModelList: SgModel[] = []; //
  apimetadataClone: SgMetadata; //
  selectedIds: string[] = [];
  private selectAllState: SelectAllCheckboxState = 'unchecked';
  // private mySelection: any[] = []; //
