  @Input() apimetadata: SgMetadata;
  newSgModel: SgModel;

  constructor(private apiService: ApiService) { }

  //
  private addServiceModel() {
    this.newSgModel = new SgModel();
    this.apimetadataClone = Object.assign({}, this.apimetadata);
  }

  //
  onSelectedKeysChange(e) {
    this.selectedServiceModelList = [] as SgModel[];
    let len = this.selectedIds.length;
    if (len > 0) {
      this.selectedIds.forEach(id => {
        if (this.apimetadata.models && this.apimetadata.models.length > 0) {
          let tempIndex = this.apimetadata.models.findIndex(value => value.id == id);
          if (tempIndex != (-1)) {
            this.selectedServiceModelList.push(this.apimetadata.models[tempIndex]);
          }
        }
      });
    }
    if (len === 0) {
      this.selectAllState = 'unchecked';
    } else if (len > 0 && len < this.apimetadata.models.length) {
      this.selectAllState = 'indeterminate';
    } else {
      this.selectAllState = 'checked';
    }
  }

  //
  private onSelectAllChange(checkedState: SelectAllCheckboxState) {
    if (checkedState === 'checked') {
      this.selectedIds = this.apimetadata.models.map((item) => item.id);
      this.selectAllState = 'checked';
    } else {
      this.selectedIds = [];
      this.selectAllState = 'unchecked';
    }
  }

  //
  private deleteServiceModelList() {
    if (!this.selectedServiceModelList || this.selectedServiceModelList.length <= 0) {
      alert('');
      return;
    }
    // let validResultDict = this.deleteServiceModelCheck();
    // if (validResultDict.key == 'true') {
    //   alert(validResultDict.value);
    //   return;
    // }
    this.selectedServiceModelList.forEach(element => {
      this.apiService.deleteSpecifiedElement(this.apimetadata.models, element);
      this.apiService.deleteSpecifiedElement(this.selectedIds,element.id);
    });
    this.selectedServiceModelList = [];
    this.selectAllState="unchecked";
  }

  //
  saveHandler(sgModel: SgModel) {
    if (!this.apimetadata.models) {
      this.apimetadata.models = [] as SgModel[];
    }
    let tempModel = {} as SgModel;
    tempModel.id = sgModel.modelId;
    tempModel.code = sgModel.code;
    tempModel.name = sgModel.name;
    tempModel.modelId = sgModel.modelId;
    tempModel.modelType = sgModel.modelType;
    this.apimetadata.models.push(tempModel);
  }

  //
  private getServiceModelTypeName(dataItem: SgModel) {
    if (dataItem.modelType == SgModelType.VO) {
      dataItem.modelTypeName = 'VO';
    }
    else if (dataItem.modelType == SgModelType.Dto) {
      dataItem.modelTypeName = 'Dto';
    }
    return dataItem.modelTypeName;
  }

