import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import { SgDesignerPackageView } from './views';
import { SgDesignerComponent } from './views/sgdesigner/sg.component';
import { Package, GSP } from '@farris/ide-devkit';

export class SgDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openSg(uri));
  }

  openSg(uri) {
    if (uri.endsWith('.eapi')) {
      const pkgView = new SgDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as SgDesignerComponent;
      currentElement.metadataUri = uri;
      // urititle
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }
}



