import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule, ConnectionBackend } from '@angular/http';
import { ApiBaseInfoComponent } from './apiBaseInfo/apibaseinfo.component';
import { ApiService } from './apiservice/apiservice';
import { SgDesignerComponent } from './sg.component';
import { CommonModule } from '@angular/common';
import { ToolBarModule } from '@progress/kendo-angular-toolbar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ButtonModule, ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';
import { LayoutModule, SplitterModule } from '@progress/kendo-angular-layout';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { GridModule } from '@progress/kendo-angular-grid';
import { DialogModule } from '@progress/kendo-angular-dialog';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { DropDownsModule, DropDownListModule } from '@progress/kendo-angular-dropdowns';
import { PopupModule } from '@progress/kendo-angular-popup';
import { ReactiveFormsModule } from '@angular/forms';
import { SgModelModalComponent } from './apiBaseInfo/apibaseinfomodal/apibaseinfodialog.component';
import { ServiceDefineComponent } from './servicedefine/servicedefine.component';
import { ResourceTypeSelectionComponent } from './servicedefine/modal/resourceselection.component';
import { ResourceOpSelectionComponent } from './servicedefine/modal/resourceopselection.component';
import { ParamEditDialogComponent } from './servicedefine/modal/paramedit.component';
import { GSPMetadataServiceModule, MetadataSelectModule } from '../../../../../reference/metadata/metadata.ref';
import { environment } from '../../../../environments/environment';
import { FarrisDialogModule } from '../../../../../reference/farris/farris.ui.ref';
import { ClipboardModule } from 'ngx-clipboard';

@NgModule({
  declarations: [
    SgDesignerComponent,
    ApiBaseInfoComponent,
    ServiceDefineComponent,
    SgModelModalComponent,
    ParamEditDialogComponent,
    ResourceTypeSelectionComponent,
    ResourceOpSelectionComponent
  ],
  imports: [
    // BrowserModule,
    FormsModule,
    // HttpModule,
    CommonModule,
    ToolBarModule,
    ButtonsModule,
    ButtonGroupModule,
    DialogModule,
    ReactiveFormsModule,
    InputsModule,
    DropDownListModule,
    PopupModule,
    TreeViewModule,
    LayoutModule,
    GridModule,
    LayoutModule,
    FarrisDialogModule,
    MetadataSelectModule,
    ClipboardModule,
    GSPMetadataServiceModule.forRoot(environment.SERVER_IP)
  ],
  providers: [/*EventBusService,ApiService*/],
  entryComponents:[ResourceTypeSelectionComponent,ResourceOpSelectionComponent,ParamEditDialogComponent],
  bootstrap: [SgDesignerComponent]
})
export class SgDesignerModule { }



