import { ComponentFactoryResolver, Injector } from '@angular/core';
import { SgDesignerComponent } from './sgdesigner/sg.component';
import { SgDesignerModule } from './sgdesigner/sg.module';
import { PackageView } from '@farris/ide-devkit';
export class SgDesignerPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(SgDesignerComponent);
    const moduleRef = this.injector.get(SgDesignerModule) as SgDesignerModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}



