import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { SgMetadata } from '../../sgmetadata/sgMetadata';
import { SelectAllCheckboxState } from '@progress/kendo-angular-grid';
import { SummaryInfo } from '../../commonobject/summaryInfo';

@Component({
    selector: 'kendo-grid-resourceopselection-form',
    templateUrl: "./resourceopselection.component.html"
})
export class ResourceOpSelectionComponent{
    private selectedOpSummaryList: SummaryInfo[] = []; //
    selectedIds: string[] = [];
    private selectAllState: SelectAllCheckboxState = 'unchecked';
    @Input() opSummaryList: SummaryInfo[];
    @Input() apimetadata: SgMetadata;

    @Output() opSelectionConfirm: EventEmitter<SummaryInfo[]> = new EventEmitter();

    onSelectedKeysChange(e) {
        this.selectedOpSummaryList = [] as SummaryInfo[];
        let len = this.selectedIds.length;
        if (len > 0) {
            this.selectedIds.forEach(element => {
                let tempIndex = this.opSummaryList.findIndex(value => value.id == element);
                if (tempIndex != (-1)) {
                    this.selectedOpSummaryList.push(this.opSummaryList[tempIndex]);
                }
            });
        }
        if (len === 0) {
            this.selectAllState = 'unchecked';
        } else if (len > 0 && len < this.opSummaryList.length) {
            this.selectAllState = 'indeterminate';
        } else {
            this.selectAllState = 'checked';
        }
    }

    private onSelectAllChange(checkedState: SelectAllCheckboxState) {
        if (checkedState === 'checked') {
            this.selectedIds = this.opSummaryList.map((item) => item.id);
            this.selectAllState = 'checked';
        } else {
            this.selectedIds = [];
            this.selectAllState = 'unchecked';
        }
    }
}



