import { Injectable } from '@angular/core';

@Injectable()

export class ApiService {
    constructor() { }

    //GuidId
    getGuid() {
        return (this.s4() + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" + this.s4() + "-" + this.s4() + this.s4() + this.s4());
    }
    s4() {
        return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }

    //
    deleteSpecifiedElement(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value == element);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1);
        }
    }

    //
    getSelectedList(value: any, input: any, inputArray: Array<any>) {
        if (value == true) {
            inputArray.push(input);
        }
        else {
            let tempIndex = inputArray.findIndex(value => value == input);
            if (tempIndex != (-1)) {
                inputArray.splice(tempIndex, 1);
            }
        }
    }

    //id
    replaceSpecifiedElementById(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value.id == element.id);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1, element);
        }
    }


    //Id
    deleteSpecifiedElementById(inputArray: Array<any>, element: any) {
        let tempIndex = inputArray.findIndex(value => value.id == element.id);
        if (tempIndex != (-1)) {
            inputArray.splice(tempIndex, 1);
        }
    }

    //json
    GetCamelParse(jsonObj: any) {
        for (let key in jsonObj) {
            if (typeof (jsonObj[key]) === "object") {
                this.GetCamelParse(jsonObj[key]);
            }
            let temp = jsonObj[key];
            delete (jsonObj[key]);
            jsonObj[this.ConvertToCamel(key)] = temp;
        }
    }
    
    //Camel
    private ConvertToCamel(input: string): string {
        if (input.length > 0) {
            let len = input.length;
            var strTemp = ""; //
            for (let i = 0; i < len; i++) {
                if (i == 0) {
                    strTemp += input[i].toLowerCase(); //
                }
                else {
                    strTemp += input[i];
                }
            }
            return strTemp;
        }
    }
}


